body {
    /* background-color: grey; */
}
span.vertical-timeline-element-icon {
    background-color: #40B0E1;
    /* #137A95 */
    cursor: pointer;
}
.vertical-timeline-element-content {
    box-shadow: none;
}
div.vertical-timeline.vertical-timeline--animate {
    /* margin-top: 0px; */
    /* margin-bottom: 0px; */
}

.vertical-timeline::before {
    background-color: black;
}
.vertical-timeline-element:nth-child(2n+1) span.vertical-timeline-element-date {
    margin-left: 8%;
}
.vertical-timeline-element:nth-child(2n) span.vertical-timeline-element-date {
    margin-right: 8%;
}
span.vertical-timeline-element-date {
    text-align: center !important;
}
img.newsimage {
    cursor: pointer;
}
h1.title {
    text-align: center;
    font-family: "Montserrat";
    height: 400px;
    /* object-fit: cover !important; */
    height: 100%;
    padding-top: 18%;
    padding-bottom: 18%;
    padding-left: 3%;
    padding-right: 3%;
    background-repeat: no-repeat !important;
    background-size: cover !important;
    background-position: center !important;
}
h1.title a {
    color: white;
}
.content {
    padding-top: 30px;
    padding-bottom: 60px;
}
.page {
    font-family: Lora;
}

.wordcloud-card {
    display: inline-block;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
}
.wordcloud-card:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}
.wordcloud-card .wordcloud-title{
    text-align: center;
    font-family: Lora;

}
.wordcloud-card-list {
    text-align: center;
}
.wordcloud-card-list .wordcloud-card {
    margin: 20px;
}
.news-metrics {
    text-align: center;
}
.paper-root {
    display: inline-block;
    width: 50%;
}
.tweet-display{
    display: inline-block;
}
.tweet-display twitter-widget {
    margin: 0 !important;
}
.paper-tweet {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-content: center;
            align-content: center;
}
.thumbnail img {
    width: 80px;
}

.CommentPaper {
    margin-bottom: 10px;
    border: grey 1px solid;
}

.CommentPaper .content {

    padding-top: 0;
    padding-bottom: 0;
    /* display: flex;
    align-content: center; */
}

.CommentPaper .content div {
  
}
.home-card {
    width: 40%;
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 10px;
    margin-bottom: 10px;
    display: inline-block;
}
.home-card img {
    height: 200px;

}
.news-card{
    width: 40%;
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 10px;
    margin-bottom: 10px;
    display: inline-block;
}
.news-card img {
    height: 200px;
}
.news-card .card-content{
    height: 70px;
}
.top-card{
    width: 100%;
    text-align: center;
}
.top-card img {
    height: 300px;
}

.media {
    object-fit: cover;
}
